import { BlockMock, DeepPartial, RewardMock } from '../core'
import { WEB3_DEFAULT_ADDR } from '../web3-client'
import {
  WalletBlockAmounts,
  WalletBlockEarnings,
  WalletBlockEarningsRewards,
  WalletBlockPerformance,
  WalletPerformance,
} from './wallet-performance.model'

/**
 * Wallet Performance Mock
 */
export function WalletPerformanceMock(
  options?: DeepPartial<WalletPerformance>
): WalletPerformance {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'WALLET_PERFORMANCE_ID',

    vaultId: options?.vaultId || 'VAULT_ID',
    vaultAddress: options?.vaultAddress || WEB3_DEFAULT_ADDR,
    walletId: options?.walletId || 'WALLET_ID',
    walletAddress: options?.walletAddress || WEB3_DEFAULT_ADDR,
    block: BlockMock(options?.block),

    startBlock: BlockMock(options?.startBlock),
    endBlock: BlockMock(options?.endBlock),

    age: options?.age || 0,

    earnings: WalletBlockEarningsMock(options?.earnings),
    fees: WalletBlockAmountsMock(options?.fees),

    realizedAPY: options?.realizedAPY || 0,
    rewardsRealizedAPY: options?.rewardsRealizedAPY || 0,
    poolSharePercentage: options?.poolSharePercentage || 0,

    accruedRewards: options?.accruedRewards?.map((a) => RewardMock(a)),
    collectedRewards: options?.collectedRewards?.map((c) => RewardMock(c)),

    deposits: WalletBlockAmountsMock(options?.deposits),
    redeemable: WalletBlockAmountsMock(options?.redeemable),

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function WalletBlockPerformanceMock(
  options?: DeepPartial<WalletBlockPerformance>
): WalletBlockPerformance {
  return {
    age: options?.age || 0,

    startBlock: BlockMock(options?.startBlock),
    endBlock: BlockMock(options?.endBlock),

    earnings: WalletBlockEarningsMock(options?.earnings),
    fees: WalletBlockAmountsMock(options?.fees),

    realizedAPY: options?.realizedAPY || 0,
    rewardsRealizedAPY: options?.rewardsRealizedAPY || 0,
    poolSharePercentage: options?.poolSharePercentage || 0,

    accruedRewards: options?.accruedRewards?.map((a) => RewardMock(a)),
    collectedRewards: options?.collectedRewards?.map((c) => RewardMock(c)),
  }
}

export function WalletBlockAmountsMock(
  options?: DeepPartial<WalletBlockAmounts>
): WalletBlockAmounts {
  return {
    token: options?.token || '0',
    USD: options?.USD || '0',
  }
}

export function WalletBlockEarningsMock(
  options?: DeepPartial<WalletBlockEarnings>
): WalletBlockEarnings {
  return {
    token: options?.token || '0',
    USD: options?.USD || '0',
    percentage: options?.percentage || 0,
    rewards: WalletBlockEarningsRewardsMock(options?.rewards),
  }
}

export function WalletBlockEarningsRewardsMock(
  options?: Partial<WalletBlockEarningsRewards>
): WalletBlockEarningsRewards {
  return {
    USD: options?.USD || '0',
    percentage: options?.percentage || 0,
  }
}
